@import '../../assets/css/variable.less';

* {
  box-sizing: border-box;
}

.token-box {
  border-top: 1px solid @divider_color;  
  a {
    text-decoration: none;
  }

  .token-info {
    width: 760px;
    border: 1px solid @divider_color;
    border-top: 0;
    margin: 0 auto;
    overflow: hidden;
    .token-header {
      display: flex;
      padding: 0 24px;
      justify-content: space-between;

      .token-header-left {
        width: 444px;

        .logo-title {
          height: 48px;
          display: flex;
          align-items: center;
          margin-top: 24px;

          .title-text {
            margin-left: 24px;
            font-size: 32px;
            color: #FFFFFF;
            font-weight: normal;
          }
        }

        .token-header-des {
          margin-top: 14px;
          font-weight: normal;
        }
      }

      .token-header-right {
        width: 242px;
        margin-top: 48px;
      }

      .learn-more {
        color: @button_outline_color;
        text-decoration: none;
      }
    }

    .token-info-content {
      width: 760px;
      display: flex;
      border-top: 1px solid @divider_color;
      border-bottom: 1px solid @divider_color;
      border-radius: 4px;
      margin-top: 73px;

      .top-info-box,
      .buttom-info-box {
        display: flex;
      }

      .info-box {

        height: 136px;
        border-right: 1px solid @divider_color;
        background: rgba(85, 119, 253, 0.1);
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;

        .num {
          font-size: 24px;
          color: @whiteFontColor;
          height: 45px;
          display: flex;
          align-items: center;
        }

        .text {
          font-size: 12px;
        }
      }

      .info-box:nth-child(4) {
        border: 0;
      }
    }

    .buy-deri-token {
      text-align: center;
      font-size: 18px;
      color: @whiteFontColor;
      margin-top: 61px;
    }

    .buy-deri-token-swap {
      display: flex;
      margin-top: 37px;

      .top-box {
        display: flex;
        justify-content: space-between;
      }

      .swap-box {

        height: 100px;
        background: rgba(85, 119, 253, 0.1);
        border: 1px solid #203B60;
        box-sizing: border-box;
        border-radius: 4px;
        display: flex;
        align-items: center;
        cursor: pointer;
        justify-content: center;
        flex-direction: column;
        .swap-name {
          font-size: 16px;
          color: @whiteFontColor;
        }

        img {
          width: 24px;
          height: 24px;
        }

      }

      .swap-box:hover {
        border: 1px solid @button_outline_color;
      }
    }
  }
}

@media only screen and (width : 760px) {
  .token-box {
    .token-info {
      border: 0;
    }
  }
}

@media only screen and (max-width : 759px) {
  .token-box {
    .token-info {
      width: 100%;
      height: 866px;
      min-width: 375px;
      border: 0;

      .token-header {
        display: block;

        .token-header-left {
          width: 100%;
        }

        .token-header-right {
          margin-top: 42px;
        }
      }

      .token-info-content {
        width: 100%;
        margin-top: 24px;
        padding: 0 24px;
        display: block;
        border: 0;

        .top-info-box,
        .buttom-info-box {
          width: 100%;
          justify-content: space-between;

          .info-box {
            width: 50%;
          }

          .min-box {
            border-right: 0;
          }
        }

        .top-info-box {
          border-bottom: 1px solid @divider_color;
        }
      }

      .buy-deri-token-swap {
        width: 100%;
        flex-wrap: wrap;
        padding: 0 24px;

        a {
          flex: 1;
          margin: 0 16px 16px 0;
          width: calc((100% - 32px) / 2);
          min-width: calc((100% - 32px) / 2);
          max-width: calc((100% - 32px) / 2);
        }

        .swap-box {
          width: 100%;
        }
      }
    }
  }
}

@media only screen and (max-width : 375px) {
  width: 100%;
  flex-wrap: wrap;
  padding: 0 24px;

  a {
    flex: 1;
    margin: 0 16px 16px 0;
    width: 156px;
  }

  .swap-box {
    width: 100%;
  }
}

@media only screen and (min-width : 760px) {
  .token-box {
    .token-info {
      height: 690px;
      .token-header {
        height: 124px;
      }
    }
    .token-info-content {
      height: 136px;
      .info-box {
        width: 190px;
      }
    }

    .buy-deri-token-swap {
      width: 760px;
      justify-content: space-between;
      .swap-box {
        width: 184px;
      }
    }
  }
}